.max-width-container {
  box-sizing: border-box;
  max-width: 1400px;
  padding: 24px 0;

  &.full {
    width: 100%;
  }
}

.main-container {
  display: grid;
  grid-template-columns: 24px 1fr 24px;

  > * {
    grid-column: 2;

    &.full {
      grid-column: 1 /-1;
    }
  }

  @include for-narrow-layout {
    grid-template-columns:
      minmax(32px, 1fr)
      minmax(auto, 900px)
      minmax(32px, 1fr);
  }
  @include for-normal-layout {
    grid-template-columns:
      minmax(32px, 1fr)
      minmax(auto, 1400px)
      minmax(32px, 1fr);
  }
}
